// 角色管理 
import http from '../httpConfig'


const table = 'mrbase_sys_role'

// 功能权限树列表
export const func_list = (params = {}) => {
    params['~table~'] = table
    return new Promise((resolve, reject) => {
        http.post('/api/role/list-function_role/v1', params)
            .then(res => {
                resolve(res)
            })
            .catch(err => {
                reject(err)
            })
    })
}
